

























import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
@Component
export default class NumberPad extends Vue {
  @Prop(Number) readonly value!: number;
  output = this.value.toString();
  isDecimalAdded = false; //判断小数点是否输入
  isOperatorAdded = false; //判断运算符运算符是否输入
  isStarted = false; //判断数字是否输入

  isOperator(character: string) {
    //判断是否是运算符号
    return ["+", "-"].indexOf(character) > -1;
  }
  append(character: string) {
    //初始值是0，并且没有输入运算符
    if (this.output === "0" && !this.isOperator(character)) {
      if (character === ".") {
        this.output += character;
        this.isDecimalAdded = true
      } else {
        this.output = character;
      }
      return;
    }
    //输入数字
    if (!this.isOperator(character)) {
      if (character === "." && this.isDecimalAdded === true) {
        return;
      }
      if (character === ".") {
        this.isDecimalAdded = true;
        this.isOperatorAdded = true
      }else{
        this.isOperatorAdded = false
      }
      
      this.output += character;
    }

    //输入运算符
    if(this.isOperator(character) && !this.isOperatorAdded){
      this.output += character
      this.isDecimalAdded = false 
      this.isOperatorAdded = true
      this.isStarted = true;
    }
  }
  calculate(str: string) {
    let fn = Function;
    return new fn("return " + str)();
  }
  getFinalResult(str:string){
      let result = parseFloat(this.calculate(str).toFixed(2)).toString()
      this.output = result
      this.isDecimalAdded = false
      this.isOperatorAdded = false
      this.isStarted = false
      if(this.output.indexOf('.')>=0){
        this.isDecimalAdded = true
      }
  }
  clear() {
    this.output = "0";
    this.isDecimalAdded = false;
    this.isOperatorAdded = false;
    this.isStarted = false;
  }
  deleteContent(){
    const lastStr = this.output.substr(this.output.length-1,1)
    if (this.output.length === 1) {
      this.output = "0";
      return 
    }
    if(lastStr === '.'){
      this.output = this.output.slice(0, -1);
      this.isDecimalAdded = false
    }else if('+-'.indexOf(lastStr) >=0){
      this.output = this.output.slice(0, -1);
      this.isOperatorAdded = false
    }else{
      this.output = this.output.slice(0, -1);
    }
  }
  ok() {
    this.$emit("update:value", parseFloat(this.output).toFixed(2));
    this.$emit("submit", parseFloat(this.output).toFixed(2));
    this.output = "0";
  }
}
