






let importAll = (webpackContext: __WebpackModuleApi.RequireContext) => {
  webpackContext.keys().forEach(webpackContext);
};
try {
  importAll(require.context("../assets/icons", true, /\.svg$/));
} catch (error) {
  console.log(error);
}
export default {
  name: "Icon",
  props: ["iconName"],
};
